*, :before, :after {
  box-sizing: border-box;
}

html {
  --color-1: var(--gray-1);
  --color-2: var(--gray-2);
  --color-3: var(--gray-3);
  --color-4: var(--gray-4);
  --color-5: var(--gray-5);
  --color-6: var(--gray-6);
  --color-7: var(--gray-7);
  --color-8: var(--gray-8);
  --color-9: var(--gray-9);
  --color-10: var(--gray-10);
  --color-11: var(--gray-11);
  --color-12: var(--gray-12);
  overflow-y: scroll;
}

@media (prefers-color-scheme: light) {
  html {
    --color-1: var(--gray-12);
    --color-2: var(--gray-11);
    --color-3: var(--gray-10);
    --color-4: var(--gray-9);
    --color-5: var(--gray-8);
    --color-6: var(--gray-7);
    --color-7: var(--gray-6);
    --color-8: var(--gray-5);
    --color-9: var(--gray-4);
    --color-10: var(--gray-3);
    --color-11: var(--gray-2);
    --color-12: var(--gray-1);
  }
}

body {
  max-width: var(--size-15);
  padding: 0 var(--size-2);
  color: var(--color-4);
  font-size: var(--size-5);
  font-family: var(--font-sans);
  background-color: var(--color-11);
  margin: 0 auto;
}

a {
  color: var(--color-4);
  font-weight: var(--font-weight-6);
  font-size: var(--size-4);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

hr {
  border-color: var(--color-6);
  width: 100%;
}

button {
  color: var(--color-0);
  font-weight: var(--font-weight-6);
  font-size: var(--size-3);
  text-align: center;
  background-color: var(--color-9);
  border: 1px solid var(--color-10);
  cursor: pointer;
  text-decoration: none;
  transition: background-color .4s, border-color .4s;
}

button.icon-btn {
  margin: var(--size-1) 0;
  padding: var(--size-4) var(--size-3);
  border-radius: var(--radius-2);
  justify-content: center;
  align-items: center;
  display: flex;
}

button.icon-btn span {
  margin-right: var(--size-2);
}

button:hover {
  background-color: var(--color-8);
  border-color: var(--color-9);
}

button:hover:disabled, button:disabled {
  color: var(--color-9);
  background-color: var(--color-7);
  border-color: var(--color-7);
  cursor: not-allowed;
}

h1 {
  font-size: var(--size-7);
}

h2 {
  font-size: var(--size-4);
}

h2.sticky {
  padding: var(--size-3) 0;
  background-color: var(--color-11);
  margin: 0;
  position: sticky;
  top: 0;
}

h3 {
  font-size: var(--size-3);
}

input[type="text"], select {
  margin: var(--size-1) 0 0 0;
  padding: var(--size-4) var(--size-3);
  color: var(--color-2);
  font-size: var(--size-3);
  background-color: var(--color-8);
  border: none;
  outline: none;
}

input[type="text"]::placeholder, select:required:invalid {
  color: var(--color-5);
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px var(--color-8) inset !important;
  -webkit-text-fill-color: var(--color-2) !important;
}

input:-webkit-autofill:hover {
  -webkit-box-shadow: 0 0 0 30px var(--color-8) inset !important;
  -webkit-text-fill-color: var(--color-2) !important;
}

input:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0 30px var(--color-8) inset !important;
  -webkit-text-fill-color: var(--color-2) !important;
}

input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px var(--color-8) inset !important;
  -webkit-text-fill-color: var(--color-2) !important;
}

select {
  cursor: pointer;
}

dialog::backdrop {
  background: #00000080;
}

.content-btn {
  padding: var(--size-4) var(--size-3);
  text-align: justify;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  display: flex;
}

.content-btn:first-of-type {
  border-radius: var(--radius-3) var(--radius-3) 0 0;
}

.content-btn:last-of-type {
  border-radius: 0 0 var(--radius-3) var(--radius-3);
}

.content-btn-title {
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 100%;
  overflow: hidden;
}

.content-btn-metadata-container {
  margin-top: var(--size-2);
  align-items: center;
  width: 100%;
  display: flex;
}

.content-btn-metadata-container * {
  color: var(--color-6);
  font-weight: var(--font-weight-5);
  font-size: var(--size-3);
}

.content-btn-info {
  font-style: italic;
}

.warning-text {
  color: var(--red-7);
}

.pbw-demo.vjs-layout-tiny {
  font-size: 8px;
}

.pbw-demo.vjs-layout-x-small {
  font-size: 10px;
}

.pbw-demo.vjs-layout-small {
  font-size: 12px;
}

.pbw-demo.vjs-layout-medium {
  font-size: 14px;
}

.pbw-demo.vjs-layout-large {
  font-size: 16px;
}

.pbw-demo.vjs-layout-x-large {
  font-size: 18px;
}

.pbw-demo.vjs-layout-huge {
  font-size: 20px;
}

.pbw-demo .vjs-title-bar {
  background: linear-gradient(#0009 0%, #0000 100%);
  padding-bottom: 2em;
}

.pbw-demo .vjs-text-track-display {
  inset: 0 0 4em;
}

.pbw-demo:hover .vjs-big-play-button {
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  background-color: #0000;
}

.pbw-demo .vjs-big-play-button {
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background: none;
  border: .1em solid #fff;
  border-radius: 50%;
  width: 2em;
  height: 2em;
  margin: 0;
  transform: translate(-50%, -50%);
}

.pbw-demo .vjs-big-play-button .vjs-icon-placeholder:before {
  position: initial;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  display: flex;
}

.pbw-demo .vjs-loading-spinner {
  border: .25em solid #0000;
  font-size: 1.2em;
}

.pbw-demo .vjs-loading-spinner, .pbw-demo .vjs-loading-spinner {
  justify-content: center;
  align-items: center;
  display: flex;
}

@keyframes vjs-spinner-fade {
  0% {
    border-top-color: #333;
  }

  20% {
    border-top-color: #333;
  }

  35% {
    border-top-color: #fff;
  }

  60% {
    border-top-color: #333;
  }

  100% {
    border-top-color: #333;
  }
}

.pbw-demo .vjs-button:hover {
  background: none;
  border: none;
}

.pbw-demo .vjs-slider {
  background-color: #ffffff4d;
  border-radius: 1em;
}

.pbw-demo .vjs-control-bar {
  background: linear-gradient(#000 0%, #0000 0%, #000000b3 100%);
  align-items: end;
  height: 6em;
  padding-inline: 1em;
}

.pbw-demo .vjs-control {
  height: 3em;
}

.pbw-demo:not(.vjs-layout-tiny, .vjs-layout-x-small, .vjs-live) .vjs-time-divider, .pbw-demo:not(.vjs-layout-tiny, .vjs-layout-x-small, .vjs-live) .vjs-current-time, .pbw-demo:not(.vjs-layout-tiny, .vjs-layout-x-small, .vjs-live) .vjs-duration {
  padding: 0;
  font-weight: bold;
  display: block;
}

.pbw-demo .vjs-current-time {
  margin-left: 1em;
}

.pbw-demo .vjs-time-divider {
  text-align: center;
  min-width: 1em;
}

.pbw-demo .vjs-remaining-time {
  display: none;
}

.pbw-demo .vjs-progress-control {
  width: calc(100% - 2em);
  height: 1em;
  position: absolute;
  bottom: 3em;
}

.pbw-demo .vjs-progress-holder {
  margin: 0;
}

.pbw-demo.vjs-layout-tiny .vjs-progress-control, .pbw-demo.vjs-layout-x-small .vjs-progress-control {
  display: flex;
}

.pbw-demo .vjs-load-progress {
  background: #ffffff4d;
  border-radius: 1em;
}

.pbw-demo .vjs-load-progress div {
  background: none;
}

.pbw-demo .vjs-play-progress {
  color: #af001e;
  background: #af001e;
  border-radius: 1em;
}

.pbw-demo .vjs-play-progress:before {
  text-shadow: 0 0 1px #000;
  font-size: 0;
}

.pbw-demo .vjs-progress-control:hover .vjs-play-progress:before {
  font-size: .9em;
}

.pbw-demo .vjs-progress-control:hover .vjs-time-tooltip {
  transform: translateX(50%);
  right: 0% !important;
}

.pbw-demo .vjs-progress-holder:focus-visible .vjs-play-progress:before {
  font-size: .9em;
}

.pbw-demo .vjs-time-tooltip, .pbw-demo .vjs-mouse-display .vjs-time-tooltip {
  color: #fff;
  text-shadow: 1px 1px 3px #000;
  background-color: #0000;
  padding: .4em;
  font-weight: bold;
  top: -3em;
}

.pbw-demo .vjs-seek-to-live-control:hover {
  background-color: #0000;
}

.pbw-demo .vjs-volume-level {
  border-radius: 1em;
}

.pbw-demo .vjs-volume-tooltip {
  text-shadow: 1px 1px 3px #000;
  background-color: #0000;
  padding: .4em;
  font-weight: bold;
  top: -2em;
}

.pbw-demo .vjs-custom-control-spacer {
  flex: auto;
  display: block;
}

.pbw-demo .vjs-menu-button-popup.vjs-hover .vjs-menu {
  display: none;
}

.pbw-demo .vjs-menu-button-popup .vjs-menu {
  bottom: 0;
  right: 0;
  left: unset;
  width: 100%;
  margin: 0;
}

.pbw-demo .vjs-menu-button-popup .vjs-menu .vjs-menu-content {
  overscroll-behavior: contain;
  background: #161616f2;
  border-radius: .5em;
  gap: .25em;
  min-width: 13em;
  padding: 1em .5em;
  display: grid;
  bottom: 5em;
  right: 50%;
  transform: translateX(50%);
}

.pbw-demo .vjs-menu-item {
  border-radius: .25em;
}

.pbw-demo .vjs-menu-item .vjs-selected {
  color: #222;
  pointer-events: none;
  background-color: #fff;
}

.pbw-demo .vjs-error-display:before {
  text-shadow: none;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  border: .1em solid #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 2em;
  height: 2em;
  margin: 0;
  padding: .75em;
  font-size: 3em;
  display: flex;
  left: 50%;
  transform: translate(-50%, -50%);
}

.pbw-demo .vjs-error-display .vjs-modal-dialog-content {
  justify-content: center;
  align-items: center;
  padding-bottom: 8em;
  display: flex;
}

.pbw-demo select {
  all: unset;
  color: #fff;
  appearance: auto;
  background: #333;
  border: none;
  margin-top: .25em;
}

.pbw-demo .vjs-modal-dialog.vjs-text-track-settings {
  overscroll-behavior: contain;
  background: #161616f2;
  border-radius: .5em;
  max-width: 25em;
  max-height: 50em;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 2px 2px 3px #0005;
}

.pbw-demo .vjs-text-track-settings .vjs-modal-dialog-content {
  flex-direction: column;
  padding: 1em 1.5em 0;
  display: flex;
}

.pbw-demo .vjs-text-track-settings legend {
  font-size: 1em;
}

.pbw-demo .vjs-text-track-settings fieldset span {
  padding-inline: 0;
}

.pbw-demo .vjs-text-track-settings fieldset {
  margin: 0 0 1em;
  padding: 0;
  display: flex;
}

.pbw-demo.video-js .vjs-text-track-settings select {
  padding: .5em;
}

.pbw-demo .vjs-track-settings-controls {
  flex-direction: row;
  justify-content: end;
  gap: 1em;
  display: flex;
}

.pbw-demo .vjs-track-settings-controls button {
  background-image: none;
  margin-block: 1em;
  padding: .25em .5em;
}

.pbw-demo .vjs-track-settings-controls button:hover {
  color: #fff;
}

.pbw-demo .vjs-track-settings-controls .vjs-default-button {
  margin-right: 0;
}

.dialog-container {
  aspect-ratio: var(--ratio-widescreen);
  background-color: var(--color-12);
  block-size: 480px;
  padding: 0;
  overflow: visible;
}

.dialog-close-btn {
  top: calc(var(--size-3) * -1);
  z-index: 2;
  width: var(--size-7);
  height: var(--size-7);
  font-size: var(--size-4);
  text-align: center;
  border-radius: var(--radius-round);
  position: absolute;
  right: 0;
}

.example-section p {
  color: var(--color-5);
  font-size: var(--size-3);
  text-align: left;
  margin-bottom: 0;
}

.load-bar-container {
  padding: 0 var(--size-3);
  background-color: var(--color-8);
  border-radius: var(--radius-2);
  align-items: center;
  display: flex;
}

.load-bar-container input {
  width: 90%;
}

.load-bar-container button {
  background-color: var(--color-8);
  border: 0;
}

.drm-settings-container {
  max-height: var(--size-15);
  flex-direction: column;
  transition: max-height .4s;
  display: flex;
}

.drm-settings-container input {
  border-radius: var(--radius-2);
}

.drm-settings-container select {
  border-right: var(--size-5) solid var(--color-8);
}

.drm-settings-container.hidden {
  max-height: 0;
  overflow: hidden;
}

.load-bar-action {
  width: 100%;
}

.pbw-title-container {
  justify-content: space-between;
  align-items: baseline;
  display: flex;
}

.pbw-title-container h1 {
  align-items: center;
  gap: .15em;
  display: flex;
}

.pbw-logo {
  height: var(--size-7);
}

.version-txt {
  color: var(--color-6);
  font-weight: var(--font-weight-4);
  font-size: var(--size-3);
  line-height: var(--size-5);
  align-self: flex-end;
}

.github-link {
  align-items: center;
  display: flex;
}

.github-icon {
  fill: var(--color-5);
}

.github-icon:hover {
  fill: var(--color-2);
}

.pbw-menu {
  margin-bottom: var(--size-4);
}

.pbw-menu a {
  padding: var(--size-1) var(--size-2);
  border-radius: var(--radius-3);
  transition: background-color .4s, color .4s;
}

.pbw-menu a.selected {
  color: var(--color-8);
  background-color: var(--color-5);
  cursor: default;
  text-decoration: none;
  transition: background-color .8s, color .4s;
}

