.search-bar-container {
  margin: 0 0 var(--size-1) 0;
  padding: 0 var(--size-2);
  background-color: var(--color-8);
  border-radius: var(--radius-2);
  align-items: center;
  display: flex;
}

.search-bar-container input {
  width: 90%;
}

.search-bar-container button {
  background-color: var(--color-8);
  border: 0;
  align-items: center;
  display: flex;
}

.results-container {
  margin-top: var(--size-3);
  flex-direction: column;
  display: flex;
}

.results-container:empty {
  gap: var(--size-3);
  min-height: var(--size-12);
  margin-top: var(--size-10);
  color: var(--gray-8);
  justify-content: center;
  align-items: center;
}

.results-container:empty:before {
  content: "";
}

.results-container:empty:after {
  font-family: var(--font-sans);
  content: "Enter Something to search";
}

.results-container:empty:before {
  font-size: var(--size-9);
}

.results-container.no-results:empty:before {
  content: "";
}

.results-container.no-results:empty:after {
  content: "No results";
}

.tree-navigation-container {
  align-items: end;
  display: flex;
}

.tree-navigation-container button {
  color: var(--color-6);
  background: none;
  border: none;
  padding: 0;
}

.tree-navigation-container button:hover {
  color: inherit;
}

.tree-navigation-container i {
  font-size: var(--size-3);
}

.spinner {
  width: var(--size-7);
  height: var(--size-7);
  border: 3px solid var(--color-2);
  border-radius: var(--radius-round);
  animation: var(--animation-spin);
  border-bottom-color: #0000;
  display: block;
}

.spinner-container {
  margin: var(--size-10);
  place-content: center;
  display: grid;
}

.spinner-container.hidden {
  display: none;
}

.intersection-observer {
  height: var(--size-9);
  margin-top: var(--size-4);
  background-color: var(--color-9);
  border: 1px solid var(--color-10);
  border-radius: var(--radius-3);
  animation: var(--animation-blink);
  width: 100%;
}

.scroll-to-top-button {
  z-index: 1;
  width: var(--size-7);
  height: var(--size-7);
  border-radius: var(--radius-round);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.scroll-to-top-button i {
  font-size: var(--size-8);
}

.fade-in {
  opacity: 0;
  animation: fade-in .4s var(--ease-elastic-in-3) forwards;
}

.spin, .spin-back {
  animation: spin .5s var(--ease-out-3) 1;
}

.spin-back {
  animation-direction: reverse;
}

.slide-up-fade-in {
  opacity: 0;
  animation: fade-in .4s var(--ease-elastic-in-3) forwards, slide-in-up .4s var(--ease-elastic-in-3);
}

